.buttons
    z-index: 5
    position: relative
    display: flex
    justify-content: space-around
    align-items: center

    .btn
        width: 70px
        height: 70px
        font-size: 36px
        text-transform: uppercase
        font-weight: bold
        border: 3px solid aquamarine
        border-radius: 50%
        background-color: white
        box-shadow: 4px 4px 10px 0px rgba(34, 60, 80, 0.24)
        transition: all 0.3s linear

        &:hover
            background-color: aquamarine
            color: white
            cursor: pointer
            box-shadow: 0px 0px 80px 10px aquamarine
